@import url("https://fonts.googleapis.com/css?family=Rokkitt");
.container {
  font-family: "Rokkitt", serif;
}

.container .title {
  margin: 50px 0 0 50px;
  font-size: 4em;
  text-shadow: 2px 4px 2px rgba(150, 150, 150, 0.46);
}

.editorContainer {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.13);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.13);
  width: 600px;
  height: 200px;
  border: 1px solid rgba(90, 93, 99, 0.3);
  background: #e1e9f5;
  margin: 50px auto 0 auto;
}

.editorContainer label {
  height: 20px;
  width: 595px;
  margin: 0 auto;
  display: block;
}

.editorContainer textarea {
  min-height: 177.5px;
  height: 177.5px;
  width: 595px;
  margin: 0 auto;
  display: block;
}

.previewContainer {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.13);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.13);
  width: 600px;
  min-height: 200px;
  border: 1px solid rgba(90, 93, 99, 0.3);
  background: #e1e9f5;
  margin: 50px auto 0 auto;
}

.previewContainer label {
  height: 20px;
  width: 595px;
  margin: 0 auto;
  display: block;
}

.previewContainer div {
  background: white;
  border: 1px solid rgba(90, 93, 99, 0.6);
  min-height: 177.5px;
  width: 595px;
  padding: 2px;
  margin: 0 auto;
  display: block;
}
